@charset "UTF-8";
.manager {
  height: 1200px; }
  .manager .container_12.menu {
    height: 240px; }
  .manager .block {
    height: 500px; }
  .manager nav {
    text-align: center; }
  .manager #menu {
    display: block;
    margin-top: 30px;
    /*Задаём стили для разделов нашего меню*/
    /*Делаем скрытую часть видимой*/ }
    .manager #menu li {
      display: inline-block;
      position: relative;
      height: 40px;
      font-size: 14px;
      font-family: 'Economica', sans-serif;
      border: 1px solid #c2c2c2;
      color: #908b8b;
      line-height: 40px;
      width: 130px;
      text-align: center;
      -webkit-box-sizing: border-box;
      -moz-box-sizing: border-box;
      box-sizing: border-box;
      /*Стили для скрытого выпадающего меню*/ }
      .manager #menu li ul {
        position: absolute;
        top: 40px;
        left: -1px;
        display: none;
        border: 1px solid #c2c2c2;
        width: 128px; }
    .manager #menu li:hover {
      background-color: #608b7e;
      color: #FFF; }
      .manager #menu li:hover ul {
        display: block; }
  .manager .box {
    margin-top: 0; }
  .manager .shown {
    display: none; }
    .manager .shown .settings .subblock {
      display: none; }
